import VueRouter from 'vue-router'
import { lazy } from './core/lazy'
import { registerApp } from './core/app/config'
import { init as initRouter } from './core/router/init'
import { DEFAULT_CONFIG } from './constants/DEFAULT_CONFIG'
import * as EVENT_TYPE from './constants/EVENT_TYPE'
import * as ERROR_CODE from './constants/ERROR_CODE'
import * as LOAD_STATUS from './constants/LOAD_STATUS'

/**
 * @typedef {import('vue-router').default} VueRouter
 * @typedef {import('vue-router').RouteConfig} VueRoute
 *
 * @typedef {Object} VueMfeRoute
 * @property {string} [parentPath] The nested parent path
 * @property {string|Array<string>} [childrenApps] The nested children app name or name array
 * @typedef {VueRoute & VueMfeRoute} Route
 *
 * @typedef {Object} VueMfeRouter
 * @property {import('vue-router').RouterOptions} [options]
 * @property {{}} [matcher]
 * @typedef {VueRouter & VueMfeRouter} Router
 *
 * @typedef {Object<string,{}>|Object<string, string[]>|Object<string, {}[]>} Resource
 *
 * @callback ResourcesFn
 * @returns {Resource|Resource[]|Promise<Resource>}
 * @typedef {ResourcesFn|Resource|Resource[]} Resources
 *
 * @typedef AppConfig
 * @property {Router} router ‰∏ªÂ∫îÁî® VueRouter Ê†πÂÆû‰æã
 * @property {boolean} [sensitive] ÊòØÂê¶ÂØπÂ§ßÂ∞èÂÜôÊïèÊÑü '/AuTh/uSEr' => '/auth/user'
 * @property {string} [parentPath] default parent path
 * @property {Resources} resources Ëé∑ÂèñËµÑÊ∫êÁöÑÈÖçÁΩÆÂáΩÊï∞ÔºåÊîØÊåÅÂêåÊ≠•/ÂºÇÊ≠•ÁöÑÂáΩÊï∞/ÂØπË±°
 *
 * @param {AppConfig} config
 *
 * 1. ÂàùÂßãÂåñË∑ØÁî±ÔºåËÆ∞ÂΩï rootApp
 * 2. Ê∑ªÂä†Èí©Â≠êÔºåÊã¶Êà™Êó†ÂåπÈÖçË∑ØÁî±
 * 3. ÊáíÂä†ËΩΩÊó†ÂåπÈÖçË∑ØÁî±ÁöÑ resources
 */
export function createApp(config) {
  // At fist, set the global config with wildcard key '*'
  registerApp(Object.assign({}, DEFAULT_CONFIG, config))

  // Then enhance router and register before-hook to intercept unmatchable route
  initRouter(config.router)
}

/**
 * createSubApp
 * @typedef {Object} SubAppConfig
 * @property {string} prefix Â≠êÂ∫îÁî®Ë¢´ÁõëÂê¨ÁöÑË∑ØÂæÑÂâçÁºÄ
 * @property {Route[]} routes Â≠êÂ∫îÁî®ÈúÄË¶ÅË¢´Âä®ÊÄÅËΩΩÂÖ•ÁöÑË∑ØÁî±
 * @property {string} [name] Â≠êÂ∫îÁî®ÂêçÁß∞
 * @property {(app)=>boolean|Error|Promise<boolean|Error>} [init] Â≠êÂ∫îÁî®ÂàùÂßãÂåñÂáΩÊï∞ÂíåÊñπÊ≥ï
 * @property {string} [parentPath] Â≠êÂ∫îÁî®Ê≥®ÂÜåÁöÑÂµåÂ•óÁà∂Ë∑ØÂæÑ
 * @property {Resources} [resources] Ëé∑ÂèñËµÑÊ∫êÁöÑÈÖçÁΩÆÂáΩÊï∞ÔºåÊîØÊåÅÂêåÊ≠•/ÂºÇÊ≠•ÁöÑÂáΩÊï∞/ÂØπË±°
 * @property {string} [globalVar] ÂÖ•Âè£Êñá‰ª∂ app.umd.js Êö¥Èú≤Âá∫ÁöÑÂÖ®ÈÉ®ÂèòÈáèÂêçÁß∞
 * @property {Object<string, Function>} [components] Êö¥Èú≤Âá∫ÁöÑÊâÄÊúâÁªÑ‰ª∂
 *
 * @param {SubAppConfig} config
 *
 * 1. ÂÆâË£ÖÂ≠êÂ∫îÁî®Ë∞ÉÁî® createSubApp ÊñπÊ≥ï
 * 2. Ë∞ÉÁî® registerApp Âà∑Êñ∞ÂÜÖÈÉ®ÁöÑÁª¥Êä§ÁöÑ configMap
 * 3. ÊâßË°å SubApp ÁöÑ init(app) => void|boolean ÊñπÊ≥ïÔºåÂàùÂßãÂåñÈ°πÁõÆÁöÑÂâçÁΩÆ‰æùËµñ
 * 4. ÂàùÂßãÂåñÊàêÂäüÂêéËøîÂõû success Âπ∂ÂÆâË£ÖÂ≠êÂ∫îÁî®Ë∑ØÁî±
 * 5. next(to) Âà∞ÂÖ∑‰ΩìÁöÑÂ≠êË∑ØÁî±ÔºåEND
 */
export function createSubApp(config) {
  // required
  if (!config.prefix) {
    throw new Error('Missing property `prefix: string` in config')
  }

  // required
  if (!config.routes) {
    throw new Error('Missing property `routes: Route[]` in config')
  }

  registerApp(config)

  return config
}

export default {
  version: '__VERSION__',
  lazy,
  createApp,
  createSubApp,
  EVENT_TYPE,
  ERROR_CODE,
  LOAD_STATUS
}
